import React from "react";
import { Icon } from '@iconify/react';
import uploadOption from '@iconify-icons/grommet-icons/upload-option';
import { Progress } from "antd";

class UploadIcon extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        isHovered: false
      };
    
      this.toggleHover = this.toggleHover.bind(this);
    }
    
    toggleHover() {
      this.setState(prevState => ({isHovered: !prevState.isHovered}));
    }
    
  
    render() {
      return (
        <span className={"hovered"} onMouseEnter={this.toggleHover} onMouseLeave={this.toggleHover}>
            {this.state.isHovered ?
                <Icon icon={uploadOption} className={this.props.items.icon.class_name} width={this.props.items.icon.width} height={this.props.items.icon.height} color={this.props.items.icon.color} /> :
                <Progress type={this.props.items.type} className={this.props.items.class_name} percent={this.props.items.days_used / this.props.items.days_total * 100} width={this.props.items.width} height={this.props.items.height} format={() => `${this.props.items.days_total - this.props.items.days_used + " " + this.props.items.measurement}`} />
            }
        </span>
      );
    }
  }

  export default UploadIcon;